import { FaChevronLeft } from "react-icons/fa";
import shopping from "../../assets/images/shopping.png";
import Button from "../../components/Button";
import ProductBox from "./ProductBox";
import { useCart } from "../../contexts/cartContext";
import { Link, useNavigate } from "react-router-dom";
import { useSidebar } from "../../contexts/sidebarContext";
import { useCheckout } from "../../contexts/checkoutContext";
import { useEffect } from "react";
import { FaHeart } from "react-icons/fa";

const Cart = () => {
  const { cartItems } = useCart();
  const { closeSidebar } = useSidebar();
  const { state, setBillingItems } = useCheckout();
  const navigate = useNavigate();

  useEffect(() => {
    setBillingItems(cartItems);
    // eslint-disable-next-line
  }, [cartItems]);

  const handleProceed = () => {
    navigate("/checkout");
    closeSidebar();
  };

  return (
    <div className="CART-SECTION h-svh flex flex-col justify-between bg-white">
      <div className="CART-HEADER flex items-center justify-between border-b border-primary p-4">
        <div className="flex items-center gap-4 w-max">
          <button onClick={closeSidebar}>
            <FaChevronLeft size={18} />
          </button>
          <h2 className="text-xl font-semibold font-secondary">Bag</h2>
        </div>

        <Link to="/wishlist" onClick={closeSidebar} className="text-primeRed">
          <FaHeart size={18} />
        </Link>
      </div>

      {cartItems.length === 0 ? (
        <div className="CARD-BODY h-full flex flex-col justify-center items-center gap-12">
          <img src={shopping} alt="shopping" className="size-40" />
          <Button className="px-6 py-3 rounded-full text-sm">
            start shopping
          </Button>
        </div>
      ) : (
        <>
          <div className="CART-ITEMS space-y-4 overflow-y-auto flex flex-col flex-grow">
            <div className="space-y-4 p-4 flex-grow">
              <div className="divide-y divide-secondary">
                {cartItems.map((item) =>
                  item.productId ? (
                    <ProductBox
                      key={item.productId._id}
                      product={item.productId}
                      quantity={item.itemQuantity}
                    />
                  ) : (
                    <div
                      key={item._id}
                      className="text-error text-center text-sm p-1"
                    >
                      Product not found
                    </div>
                  )
                )}
              </div>
            </div>

            <div className="p-4 bg-offWhite">
              <table className="w-full divide-y divide-primary">
                <tbody>
                  <tr>
                    <td className="text-sm text-secondary">
                      Subtotal ({state?.billingItems?.length})
                    </td>
                    <td className="text-sm text-right">₹ {state.subtotal}</td>
                  </tr>
                  <tr>
                    <td className="text-sm text-secondary">Discount</td>
                    <td className="text-sm text-right text-success">
                      - ₹ {state.discount}
                    </td>
                  </tr>
                  {state.utsavDiscount > 0 && (
                    <tr>
                      <td className="text-sm text-secondary">Utsav Discount</td>
                      <td className="text-sm text-right text-success">
                        - ₹ {state.utsavDiscount}
                      </td>
                    </tr>
                  )}
                  <tr>
                    <td className="text-sm text-secondary">GST (incl)</td>
                    <td className="text-sm text-right text-primary">
                      ₹ {state.tax.toFixed(2)}
                    </td>
                  </tr>
                  <tr>
                    <td className="text-sm text-secondary pb-2">Shipping</td>
                    <td className="text-sm text-right pb-2">
                      {state.shippingCost > 0 ? (
                        `₹ ${state.shippingCost}`
                      ) : (
                        <p className="text-xs text-green-500">Free delivery</p>
                      )}
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td className="text-sm font-semibold pt-1">Total</td>
                    <td className="text-right font-bold pt-1">
                      ₹ {state.total}
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>

          <div className="CART-FOOTER p-4 flex items-center justify-between shadow-upper z-[2]">
            <div className="flex flex-col justify-between text-start">
              <p className="text-secondary text-sm">Total</p>
              <p className="font-bold text-lg leading-none">₹ {state.total}</p>
            </div>

            <Button onClick={handleProceed}>Place Order</Button>
          </div>
        </>
      )}
    </div>
  );
};

export default Cart;
