import { FaHeart, FaRegHeart } from "react-icons/fa";
import { Link, useNavigate } from "react-router-dom";
import { useCart } from "../contexts/cartContext";
import { useWishlist } from "../contexts/wishlistContext";
import RatingBadge from "./RatingBadge";
import { useRatingDetails } from "../services/productService";
import Button from "./Button";
import { useState } from "react";
import Carousel from "./Carousel";
import Cart from "../layouts/Cart";
import { useSidebar } from "../contexts/sidebarContext";
import utsavBadge from "../assets/images/utsav-badge.png";

const ProductBox = ({ product, dropdown = false }) => {
  const navigate = useNavigate();
  const { addToCart, itemInCart } = useCart();
  const { addToWishlist, removeFromWishlist, isWished } = useWishlist();
  const { data, isLoading: ratingLoading } = useRatingDetails(
    product.productGroup._id
  );
  const [isHovered, setIsHovered] = useState(false);
  const { openSidebar } = useSidebar();

  const handleCart = () => {
    if (itemInCart(product._id)) {
      openSidebar(<Cart />);
    } else {
      addToCart(product);
    }
  };

  const toggleWishlist = () => {
    if (isWished(product._id)) {
      removeFromWishlist(product._id);
    } else {
      addToWishlist(product._id);
    }
  };

  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className={`size-full flex flex-col border border-secondary rounded-md overflow-hidden hover:shadow-md ${
        product.quantity <= 0 && "opacity-75"
      }`}
    >
      <div
        onClick={() => navigate(`/product/${product._id}`)}
        className="ITEM-IMAGES relative w-full pt-[100%] cursor-pointer"
      >
        <div className="absolute inset-0 overflow-hidden">
          {isHovered &&
          product?.productGroup?.otherImages &&
          product?.productGroup?.thumbnail ? (
            <Carousel pagination autoplay delay={2000} loop>
              {[
                ...product?.images,
                ...product?.productGroup?.otherImages,
                ...product?.productGroup?.thumbnail,
              ].map((image, index) => (
                <img
                  key={index}
                  src={image}
                  alt={index}
                  className="size-full object-contain object-center"
                />
              ))}
            </Carousel>
          ) : (
            <img
              src={product?.images[0]}
              alt={product?.productGroup?.name}
              className="size-full object-contain object-center"
            />
          )}
        </div>

        <div className="absolute bottom-2 left-2 z-[5]">
          {!ratingLoading && data?.averageRating > 0 && (
            <RatingBadge
              rating={data.averageRating}
              totalRatings={data.totalCount}
              ratingsBreakdown={data.ratingCounts}
              dropdown={dropdown}
            />
          )}
        </div>
        {product?.isUtsav && (
          <div className="absolute top-2 left-2 z-[5]">
            <img src={utsavBadge} alt="utsav" className="h-2" />
          </div>
        )}
      </div>

      <div className="ITEM-DETAILS p-2 lg:space-y-1 font-secondary flex-grow">
        <Link
          to={`/brand/${product?.productGroup?.brand?._id}`}
          className="BRAND font-medium text-xs max-lg:text-xxs text-secondary leading-none"
        >
          <p>{product?.productGroup?.brand?.name}</p>
        </Link>
        <p className="NAME text-sm max-lg:text-xs font-medium line-clamp-2 leading-none">
          {product?.name}
        </p>
        {/* <ul className="ATTRIBUTES flex items-center gap-1 py-1 line-clamp-1 truncate">
          {Object.values(product.attributes).map((value, index) => (
            <li
              key={index}
              className="text-xs max-lg:text-xxs text-secondary capitalize leading-none"
            >
              {index > 0 && "• "}
              {value}
            </li>
          ))}
        </ul> */}
        <div className="PRICING flex items-center lg:gap-2 gap-1 whitespace-nowrap">
          <p className="text-xs max-lg:text-xxs font-medium text-primary line-through leading-none">
            ₹ {Math.round(product?.unitPrice)}
          </p>
          <p className="font-bold text-lg max-lg:text-sm leading-none">
            ₹ {Math.round(product?.sellingPrice)}
          </p>
          {product?.discount > 0 && (
            <p className="text-sm max-lg:text-xxs font-medium text-blue-600 leading-none">
              {product?.discountType === "percent"
                ? `${product.discount}%`
                : `₹${product.discount} OFF`}
            </p>
          )}
        </div>
        <div className="UTSAV-PRICING flex items-center lg:gap-2 gap-1 whitespace-nowrap text-success">
          <p className="font-medium leading-none max-lg:text-xs">Utsav Price</p>
          <p className="text-lg max-lg:text-sm font-bold leading-none">
            ₹ {Math.round(product.utsavPrice)}
          </p>
        </div>
      </div>

      <div className="BTN-SECTION flex justify-center gap-4 max-lg:gap-2 p-2 pt-0">
        {product.quantity > 0 ? (
          <Button
            reverse
            onClick={handleCart}
            className="h-10 max-lg:h-8 p-2 flex items-center justify-center rounded"
          >
            {itemInCart(product._id) ? (
              <p className="text-sm leading-none">GO TO BAG</p>
            ) : (
              <p className="text-sm leading-none">ADD TO BAG</p>
            )}
          </Button>
        ) : (
          <Button
            type="button"
            reverse
            className="h-10 max-lg:h-8 p-2 flex items-center justify-center rounded"
          >
            <p className="text-sm leading-none">SOLD OUT</p>
          </Button>
        )}
        <Button
          reverse
          onClick={toggleWishlist}
          className="size-10 max-lg:size-8 flex items-center justify-center flex-shrink-0 rounded"
        >
          {isWished(product._id) ? (
            <FaHeart className="text-primeRed flex-shrink-0" size={18} />
          ) : (
            <FaRegHeart className="text-primeRed flex-shrink-0" size={18} />
          )}
        </Button>
      </div>
    </div>
  );
};

export default ProductBox;
